	module.exports = {"data":{"markdownRemark":{"html":"<p>I was privileged to be able to create a display case and storefront for the beautiful works of Hyeran Lee.</p>\n<p>Made with React and Material-UI, and peppered with styled-components and Materialize CSS. It also uses React's Context API to flexibly share data between components without installing Redux.</p>\n<p>Now that I'm familiar with React, I'm really enjoying its low overhead, lush ecosystem, and enthusiastic community.</p>\n<p>This work is currently in progress, waiting for design feedback before the next iteration.</p>","frontmatter":{"id":"7","path":"/dc_gatsby/2018-7__hl_storefront","caption":"A responsive display case and storefront for the abstract works of Hyeran Lee.","title":"Hyeran Lee Art","year":"2018","radius":"8.5","tools":["React","Material-UI","styled-components","SCSS","Materialize CSS","GraphQL","Node.js"],"image":"https://image.ibb.co/m9qUuz/hl_storefront.jpg","imgThumb":"https://image.ibb.co/j6UHge/hl_storefront_logo.jpg","website":"https://dcorn068.github.io/hl_storefront/"}}},"pathContext":{}}

